.nav {
  &__list {
    display: flex;
  }

  &__item {
    margin-right: 64px;
  }

  &__link {
    @include navLink;

    &--mobile {
      display: none;
    }

    &:hover {
      background-size: 100% 2px;
    }
  }

}

@media (max-width: 1024px) {
  .nav__item {
    margin-right: 30px;
  }
}

@media (min-width: 820px) {
  .menu__toggle {
    display: none;
  }
}

@media (max-width: 820px) {
  .header__nav {
    position: relative;
  }

  .link.nav__link {
    font-size: 22px;
    line-height: 22px;
  }

  .nav {
    &__list {
      display: flex;
      flex-direction: column;
      position: absolute;
      align-items: center;
      visibility: hidden;
      top: -100%;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100vw;
      height: 100%;
      padding-top: 150px;
      background-color: #008aff;
      transition-duration: 0.3s;
    }

    &__item {
      margin-right: 0;
    }

    &__link {
      display: block;
      padding: 12px 24px;
      color: #fff;
      border-radius: 15px;
      font-weight: bold;
      font-size: 22px;
      line-height: 22px;
      text-decoration: none;
      transition-duration: 0.3s;

      &--mobile {
        display: block;
      }
    }

    &__link:hover {
      background-size: 100% 2px;
    }

    &.header__nav {
      position: static;
    }

    &__item:nth-last-child(-n+2) {
      display: block;
    }
  }

  .link.nav__link.nav__link--small {
    font-size: 15px;
    line-height: 16px;
  }

  .link.nav__link.nav__link--underline {
    font-size: 15px;
    line-height: 16px;
    text-decoration: underline;
  }

  .menu {
    &__toggle {
      opacity: 0;
      position: absolute;
      top: -100%;
    }

    &__span,
    &__span::before,
    &__span::after {
      display: block;
      position: absolute;
      width: 100%;
      height: 4px;
      background-color: #fff;
      transition-duration: 0.3s;
    }

    &__span::before {
      content: "";
      top: -8px;
    }

    &__span::after {
      content: "";
      top: 8px;
    }

    &__toggle:checked ~ .menu__btn > .menu__span {
      transform: rotate(45deg);
    }

    &__toggle:checked ~ .menu__btn > .menu__span::before {
      top: 0;
      transform: rotate(0);
    }

    &__toggle:checked ~ .menu__btn > .menu__span::after {
      top: 0;
      transform: rotate(90deg);
    }

    &__toggle:checked ~ .nav__list {
      visibility: visible;
      top: 0;
    }

    &__btn {
      display: block;
      position: absolute;
      right: 5%;
      top: 55px;
      width: 40px;
      height: 30px;
      cursor: pointer;
      z-index: 1;
    }
  }
}
